import { StaticImage } from "gatsby-plugin-image"
import React from "react"

export default function Herobook() {
    return (<div>
        <h2>Herobook</h2>
        <StaticImage className='rounded-full w-96 text-center'
                    alt="Pourtrait de l'auteur"
                    src="../images/portrait-link-square2.jpg"
                />
        <p>
            Herobook est un moteur de jeu textuel permettant de réaliser des histoires à embranchement
            ou des romans dont vous êtes les héros. Son dévelopement à début en avril 2014 et est toujours
            actif.
        </p>
    </div>)
}